import $$ from 'dom7';
import Framework7 from 'framework7/framework7.esm.bundle.js';

// Import F7 Styles
import 'framework7/css/framework7.bundle.css';

// Import Icons and App Custom Styles
import '../css/icons.css';
import '../css/app.css';
// Import Cordova APIs
import cordovaApp from './cordova-app.js';
// Import Routes
import routes from './routes.js';

var app = new Framework7({
  root: '#app', // App root element
  id: 'id.harnic.courier', // App bundle ID
  name: 'Harnic Courier', // App name
  theme: 'auto', // Automatic theme detection
  // App root data
  data: function () {
    return {
      user: {
        firstName: 'John',
        lastName: 'Doe',
      },

    };
  },
  // App root methods
  methods: {
    helloWorld: function () {
      app.dialog.alert('Hello World!');
    },
  },
  // App routes
  routes: routes,


  // Input settings
  input: {
    scrollIntoViewOnFocus: Framework7.device.cordova && !Framework7.device.electron,
    scrollIntoViewCentered: Framework7.device.cordova && !Framework7.device.electron,
  },
  // Cordova Statusbar settings
  statusbar: {
    iosOverlaysWebView: true,
    androidOverlaysWebView: false,
  },
  on: {
    init: function () {
      var f7 = this;
      if (f7.device.cordova) {
        // Init cordova APIs (see cordova-app.js)
        cordovaApp.init(f7);
      }
    },
  },
});

//INIT PAGE

$$(document).on('page:init', '.page[data-name="dashboard"]', function (e) {


  var datalogin = app.form.getFormData('#login-info');
  var user = app.form.getFormData('#auth-info');
  console.log('DATA'+user);

if (!datalogin && !user) {
  console.log('Belum Login');
  app.loginScreen.open('#login-screen');
  // CEK LOGIN
  $$('#login').on('click', function () {
    // alert('Log In');


    var contact = $$('#login-info [name="contact"]').val();
    var password = $$('#login-info [name="password"]').val();
    if (contact != "" && password != "") {
      app.preloader.show();
      //API LOGIN
      app.request.post('https://api.harnic.id/c/login', { contact: contact, password: password }, function (data) {

        // console.log(data);
        
        var json = JSON.parse(data);

        // console.log(json.success);

        if (json.success == true) {

          app.form.storeFormData('#login-info', {
            'contact': contact,
            'password': password
          });

          app.form.storeFormData('#auth-info', {
            'kurir_id': json.kurir_id,
            'kurir_nama': json.kurir_nama,
            'token': json.token
          });


          $$('#kurir_nama').text(json.kurir_nama);
          getPick(json.kurir_id);
          app.preloader.hide();
          app.loginScreen.close('#login-screen');

        }else{
          app.preloader.hide();
          var toastWithButton = app.toast.create({
            text: "Login Error",
            closeButton: true,
          });
          toastWithButton.open();
        }
      });

    }else{
      var toastWithButton = app.toast.create({
        text: 'Masukkan data login anda',
        closeButton: true,
      });
      toastWithButton.open();
    }
  });
}else{
  console.log(user.kurir_id);
  $$('#kurir_nama').text(user.kurir_nama);
  getPick(user.kurir_id);

  //PICKUP
  $$('#pickup').on('click', function () {
    // alert();
    var resi = $$('#pengiriman [name="resi"]').val();
    var action = 0;
    var token = user.token;
    // alert(resi+action+token);

    app.preloader.show();

    app.request({
      url: 'https://api.harnic.id/courier/createLog',
      method: 'POST',
      contentType: 'application/x-www-form-urlencoded',
      headers: {
        token: token
      },
      data: {
        kurir_id: user.kurir_id,
        resi: resi,
        action: action
      },
      success: function(data){
       app.preloader.hide();
        var d = JSON.parse(data);
        app.dialog.alert(d.message,'System Alert');
        getPick(user.kurir_id);
      },
      error: function (xhr, ajaxOptions, thrownError) { // Ketika ada error
        console.log(xhr.responseText); 
      }
    });
  });

  //TRANSIT
  $$('#transit').on('click', function () {
    var resi = $$('#pengiriman [name="resi"]').val();
    var action = 1;
    var token = user.token;
    // alert(resi+action+token);

    app.preloader.show();

    app.request({
      url: 'https://api.harnic.id/courier/createLog',
      method: 'POST',
      contentType: 'application/x-www-form-urlencoded',
      headers: {
        token: token
      },
      data: {
        kurir_id: user.kurir_id,
        resi: resi,
        action: action
      },
      success: function(data){
        app.preloader.hide();
        var d = JSON.parse(data);
        app.dialog.alert(d.message,'System Alert');
        getPick(user.kurir_id);
      },
      error: function (xhr, ajaxOptions, thrownError) { // Ketika ada error
        console.log(xhr.responseText); 
      }
    });
  });

  //RECEIVED
  $$('#received').on('click', function () {
    var resi = $$('#pengiriman [name="resi"]').val();
    var action = 2;
    var token = user.token;
    // alert(resi+action+token);

    app.preloader.show();

    app.request({
      url: 'https://api.harnic.id/courier/createLog',
      method: 'POST',
      contentType: 'application/x-www-form-urlencoded',
      headers: {
        token: token
      },
      data: {
        kurir_id: user.kurir_id,
        resi: resi,
        action: action
      },
      success: function(data){
        app.preloader.hide();
        var d = JSON.parse(data);
        app.dialog.alert(d.message,'System Alert');
        getPick(user.kurir_id);
      },
      error: function (xhr, ajaxOptions, thrownError) { // Ketika ada error
        console.log(xhr.responseText); 
      }
    });
  });

  //SCAN
  $$('#resi').on('click', function () {
    if (window.cordova) {
      cordova.plugins.barcodeScanner.scan(
        function (result) {
          document.getElementById('resi').value = result.text;
        },
        function (error) {
            alert("Scanning failed: " + error);
        }
     );
    }

  });
}




  


  //LOGOUT
  $$('#logout').on('click', function () {
    // console.log('Bye');
    app.form.removeFormData('#login-info');
    app.form.removeFormData('#auth-info');
    app.loginScreen.open('#login-screen');
    // console.log('Berhasil keluar');
  });

  function getPick(courier_id){
    app.request({
      url: 'https://api.harnic.id/courier/getCourierPick/' + courier_id,
      method: 'GET',
      success: function(data){
        var d = JSON.parse(data);
        // console.log(d.length);

        $$('#total_pickedup').text(d.length);
        $$('#pickedup').html('');
        d.forEach(el => {
          // console.log(el);
        $$('#pickedup').append('<li class="accordion-item">\
                <a href="" class="item-link item-content">\
                    <div class="item-inner">\
                        <div class="item-title">'+el.province+'/'+el.city+'</div>\
                    </div>\
                </a>\
                <div class="accordion-item-content">\
                  <div class="card">\
                    <div class="card-content card-content-padding">\
                      <strong>'+el.customer+'</strong><br>\
                      '+el.phone+'\
                      <p>'+el.addr+'</p>\
                    </div>\
                  </div>\
                </div>\
            </li>');
        });
      },
      error: function (xhr, ajaxOptions, thrownError) { // Ketika ada error
        console.log(xhr.responseText); 
      }
    });
  }
});